.tradingview-widget-container-list-main {
  position: relative;
}
.tradingview-widget-container-list-click {
  position: sticky;
  z-index: 2;
}
.tradingview-widget-container-list-click ul li {
  height: 60.2px;
  width: 100%;
  cursor: pointer;
}
.tradingview-widget-container-list {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
}
.coinlist_overflow{
  overflow-y: "auto";


}
.coinlist_overflow::-webkit-scrollbar{
width: 3.9px;
}
.coinlist_overflow::-webkit-scrollbar-track{
  background: rgb(43 43 64 / 61%);
}
.coinlist_overflow::-webkit-scrollbar-thumb{
   background-color: rgb(108 108 120);
        border-radius: 20px;
}


.trade_modal .ant-modal,.trade_modal .ant-modal-content {
  margin: 0;
  top: 0;
  padding: 50px 12px 0px 12px !important;
}
 .trade_modal  .ant-modal-body {
  height: calc(100vh - 110px);
 }

 .trade_modal .ant-segmented .ant-segmented-item-selected {
  background-color: #34404f !important;

}

